import manageList from '@/components/manageDev/list'
import manageDetail from '@/components/manageDev/detail'


export default [
    {
        path:"/manageDev/list",
        component: manageList
    },
    {
        path:"/manageDev/detail",
        component: manageDetail,
        name: 'manageDetail'
    }
]


